import {Component, ViewChild, ElementRef, OnInit} from '@angular/core';
import { TabComponent } from "@syncfusion/ej2-angular-navigations";
import { ButtonComponent } from '@syncfusion/ej2-angular-buttons';
import { DialogComponent } from '@syncfusion/ej2-angular-popups';
import { EmitType } from '@syncfusion/ej2-base';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{
  @ViewChild('tabObj', {static: false}) public tabObj: TabComponent;

  @ViewChild('toggleAdvancedSettings', { static: false })
  toggleAdvancedSettingsButton: ButtonComponent;

  @ViewChild('ejDialog', { static: false }) ejDialog: DialogComponent;
  // The Dialog shows within the target element.
  @ViewChild('container', { read: ElementRef,static: false }) container: ElementRef;
  // The Dialog shows within the target element.
  public targetElement: HTMLElement;

  public headerText: Object = [{ 'text': '' }, { 'text': '' },{ 'text': '' },{ 'text': '' }
    ,{ 'text': '' },{ 'text': '' }];

  advancedSettingsVisible = false;

  constructor(private translate: TranslateService) {
    translate.setDefaultLang('ch');
  }

  private toggleAdvanceSettings() {
    this.advancedSettingsVisible = !this.advancedSettingsVisible;
    if (this.advancedSettingsVisible) {
      this.toggleAdvancedSettingsButton.iconCss =
        'lh-icon lh-icon-arrow-expand';
    } else {
      this.toggleAdvancedSettingsButton.iconCss = 'lh-icon lh-icon-arrow-next';
    }
  }
  // To get all element of the dialog component after component get initialized.
  ngOnInit() {
    this.initilaizeTarget();
  }

  // Initialize the Dialog component target element.
  public initilaizeTarget: EmitType<object> = () => {
    this.targetElement = this.container.nativeElement.parentElement;
  }
  public onOpenDialog = function(event: any): void {
    // Call the show method to open the Dialog
    this.ejDialog.show();
  };
  // Sample level code to hide the Dialog when click the Dialog overlay
  public onOverlayClick: EmitType<object> = () => {
    this.ejDialog.hide();
  }
}
