import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import {HttpClientModule} from "@angular/common/http";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {AlumniHeaderComponent} from "./header/alumni-header.component";
import {SidebarModule, TabModule} from "@syncfusion/ej2-angular-navigations";
import {AlumniFooterComponent} from "./footer/alumni-footer.component";
import {DialogModule} from "@syncfusion/ej2-angular-popups";
import {DisplayerComponent} from "./displayer/displayer.component";
import {NotificationComponent} from "./notification/notification.component";
import {ActivityComponent} from "./activity/activity.component"
import {AlumniLoginComponent} from "./login/alumni-login.component";
import {DockerComponent} from "./docker/docker.component";
import {MatTabsModule} from '@angular/material/tabs';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {MatListModule, MatSidenavModule} from "@angular/material";
import { ButtonModule } from '@syncfusion/ej2-angular-buttons';
import {BsDropdownModule} from 'ngx-bootstrap/dropdown';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {HttpClient} from '@angular/common/http';

@NgModule({
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    FormsModule,
    TabModule,
    DialogModule,
    ReactiveFormsModule,
    SidebarModule,
    MatTabsModule,
    BrowserAnimationsModule,
    MatSidenavModule,
    MatListModule,
    ButtonModule,
    BsDropdownModule.forRoot(),
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  providers: [],
  declarations: [
    AppComponent,
    AlumniHeaderComponent,
    AlumniFooterComponent,
    DisplayerComponent,
    NotificationComponent,
    ActivityComponent,
    AlumniLoginComponent,
    DockerComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}
